import React, { Component } from 'react';
import Card from 'react-bootstrap/Card'
import { Button, Table } from 'react-bootstrap';
import Form from 'react-bootstrap/Form';
import Header from "../Header";
import Footer from "../Footer";
import { Link } from 'react-router-dom'
import { useHistory } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import ClipLoader from "react-spinners/ClipLoader";
import axios from "axios";
import { withRouter } from 'react-router-dom';
import Pagination from "@material-ui/lab/Pagination";
class AgentDetails extends Component {

    state = {
        userId: '', agentId: '', item: [], error: '', isLoading: false, loadingColor: '#ffffff', pageId: 1, mobileNo: '', firstName: '', lastName: '', mobileNo: '', email: '', adharNo: '', addharLink: ''
        , licenseLink: '', rcLink: '', officeAddress: '', companyName: '', companyReglink: '', addharError: '',accountStatus:'pending',
        licenseError: '', rcError: '', companyError: '', companyVerified: 'N', adharVerified: 'N', licenseVerified: 'N', rcVerified: 'N'
    };
    constructor(props) {
        super(props);
        //console.log("++pickup**********==="+JSON.stringify(this.props));    
    }

    componentDidMount() {
        this.setState({ isLoading: true });
        let userId = localStorage.getItem("userId");
        this.setState({ userId: userId });

        this.setState({ agentId: this.props.match.params.agentId });

        this.getAgent(userId, this.props.match.params.agentId);
    }

    showPopup(object) {

        this.prePayment(object);
    }
    async getAgent(userId, agentId) {

        //const headers = { 'Content-Type': 'application/json' } 
        let token = localStorage.getItem("token");
        const headers = { 'Authorization': `Bearer ${token}` };
        let urlData = "&userId=" + userId + "&agentId=" + agentId;
        const response = await fetch(process.env.REACT_APP_APIURL + 'admin/get_agentbyid?' + urlData, { headers });
        console.log("+++response==" + JSON.stringify(response))
        const dataRes = await response.json();
        if (dataRes.code == 200) {
            //agentData=dataRes.data.agentData
            this.setState({ item: dataRes.data });
            this.setState({ mobileNo: dataRes.data.mobileNo });
            this.setState({ firstName: dataRes.data.firstName });
            this.setState({ lastName: dataRes.data.lastName });
            this.setState({ email: dataRes.data.email });
            this.setState({ status: dataRes.data.status });
            this.setState({ addharLink: dataRes.agentData.adharLink })
            this.setState({ licenseLink: dataRes.agentData.licenseLink })
            this.setState({ rcLink:dataRes.agentData.rcLink })
            this.setState({ companyName: dataRes.agentData.companyName })
            this.setState({ companyReglink:dataRes.agentData.registrationCopy })
            this.setState({ officeAddress: dataRes.agentData.officeAddress })
            this.setState({ companyVerified: dataRes.agentData.companyVerified })
            this.setState({ adharVerified: dataRes.agentData.adharVerified })
            this.setState({ licenseVerified: dataRes.agentData.licenseVerified })
            this.setState({ rcVerified: dataRes.agentData.rcVerified })
            this.setState({ accountStatus: dataRes.agentData.accountStatus })
            
        } else {
            console.log("errorr")
            //this.setState({error:'No data found'})
        }
        this.setState({ isLoading: false });
        //this.setState({cabsList:data.data});
    }

    invalidAgent = async (value) => {
        try {
            
            let token = localStorage.getItem("token");
            
            const headers = { 'Authorization': `Bearer ${token}` };
            //let urlData = "&userId=" + this.state.userId + "&driverId=" + obj._sid + "&status=block";
            const urlData = { userId: this.state.userId, agentId: this.state.agentId, type: value,status:'invalid' };
            const result = await axios.put(process.env.REACT_APP_APIURL + "admin/update_agent_status", urlData, { headers });
            console.log(result.data + "resultpay:" + JSON.stringify(result.data));
            if (result.data.code == 200) {
                this.setState({ error: 'Car blocked' });
                this.getAgent(this.state.userId, this.state.agentId);
            } else {
                this.setState({ error: 'Something went wrong' });
            }
        } catch (err) {
            console.log("error:" + JSON.stringify(err.response));
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }
    }
    deleteAgent = async (value) => {
        try {
            let token = localStorage.getItem("token");
            
            const headers = { 'Authorization': `Bearer ${token}` };
            const urlData = { userId: this.state.userId, agentId: this.state.agentId, type: value,status:'delete' };
            const result = await axios.put(process.env.REACT_APP_APIURL + "admin/update_agent_status", urlData, { headers });
            console.log(result.data + "resultpay:" + JSON.stringify(result.data));
            if (result.data.code == 200) {
                this.setState({ error: 'Car deleted' });
                this.getAgent(this.state.userId, this.state.agentId);
            } else {
                this.setState({ error: 'Something went wrong' });
            }
        } catch (err) {
            console.log("error:" + JSON.stringify(err.response));
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }
    }
    approveAgent = async (value) => {
        try {
            let token = localStorage.getItem("token");
            
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + this.state.userId + "&agentId=" + this.state.agentId + "&type="+value+ "&status=approve";
            const result = await axios.put(process.env.REACT_APP_APIURL + "admin/update_agent_status", urlData, { headers });
            console.log(result.data + "resultpay:" + JSON.stringify(result.data));
            if (result.data.code == 200) {
                this.setState({ error: 'Car approved' });
                this.getAgent(this.state.userId, this.state.agentId);
            } else {
                this.setState({ error: 'Something went wrong' });
            }
        } catch (err) {
            console.log("error:" + JSON.stringify(err.response));
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }
    }

    updateAgent = async (value) => {
        try {
            let token = localStorage.getItem("token");
            
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + this.state.userId + "&agentId=" + this.state.agentId + "&status="+value;
            console.log("Data:="+urlData)
            //const result = await axios.get(process.env.REACT_APP_APIURL + "admin/agent_activate", urlData, { headers });
            const result = await axios.put(process.env.REACT_APP_APIURL + "admin/agent_activate", urlData, { headers });
            console.log(result.data + "resultpay:" + JSON.stringify(result.data));
            if (result.data.code == 200) {
                this.setState({ error: 'status updated' });
                if(value=='delete'){
                    window.location.href="/admin/AllAgents";
                }else{
                    this.getAgent(this.state.userId, this.state.agentId);
                }
                
            }else {
                this.setState({ error: 'Something went wrong' });
            }
        } catch (err) {
            console.log("error:" + JSON.stringify(err.response));
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: 'Something went wrong' });
        }
    }
    render() {
        const override = `
        display: block;
        margin: 0 auto;
        border-color: red;
        position: absolute;
        z-index: 9;
        margin: 0px auto;
        left: 40%;
        `;
        return (

            <div>
                <Header />
                <div>
                    <ClipLoader css={override} style={{ borderColor: 'red' }} color={this.state.loadingColor} loading={this.state.isLoading} size={150} >  </ClipLoader>
                </div>


                <div>

                    <section id="pricing" className="pricing">

                        <div className="container" data-aos="fade-up" style={{ width: '95%!important' }}>

                            <div className="row">
                                <div className="col-lg-12 col-md-12">

                                    <Card>
                                        <Card.Title className="bgColor" style={{ fontSize: 16, fontWeight: 'bold', borderRadius: "5px 5px 0px 0px", padding: 10, color: 'white' }}>
                                            Profile {this.state.accountStatus=='active' ? <span><i className="fa fa-check" style={{ color: 'green', fontSize: 20 }} aria-hidden="true" /></span> : null}
                                            </Card.Title>
                                        <Card.Body>
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>First Name</Form.Label>
                                                        <Form.Control type="text" placeholder="First Name" value={this.state.firstName} />
                                                    </Form.Group>
                                                </div>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail">
                                                        <Form.Label>Last Name</Form.Label>
                                                        <Form.Control type="text" placeholder="Last Name" value={this.state.lastName} />
                                                    </Form.Group>
                                                </div>
                                            </div>

                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Mobile No.</Form.Label>
                                                        <Form.Control type="text" placeholder="Mobile No" value={this.state.mobileNo} />
                                                    </Form.Group>
                                                </div>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Email</Form.Label>
                                                        <Form.Control type="email" placeholder="Email" value={this.state.email} />
                                                    </Form.Group>
                                                </div>
                                            </div>
                                            <div className="col-12">
                                                {this.state.accountStatus=='pending'?
                                                <Form.Group controlId="formBasicEmail" style={{ float: 'right' }}>
                                                    
                                                    <Button variant="primary" style={{marginRight:5}} type="button" onClick={this.updateAgent.bind(this, 'approve')}>
                                                        Activate
                                                    </Button>
                                                    
                                                    <Button variant="primary" style={{marginRight:5}} type="button" onClick={this.updateAgent.bind(this, 'delete')}>
                                                        Delete
                                                    </Button>
                                                </Form.Group>:<Form.Group controlId="formBasicEmail" style={{ float: 'right' }}>
                                                    
                                                    <Button variant="primary" style={{marginRight:5}} type="button" onClick={this.updateAgent.bind(this, 'deactivate')}>
                                                        Deactivate
                                                    </Button>
                                                    <Button variant="primary" style={{marginRight:5}} type="button" onClick={this.updateAgent.bind(this, 'delete')}>
                                                        Delete
                                                    </Button>
                                                </Form.Group>
    }
                                            </div>
                                        </Card.Body>
                                    </Card>
                                </div>
                                <div className="col-lg-12 col-md-12" style={{ marginTop: 10 }}>
                                    <Card>
                                        <Card.Title className="bgColor" style={{ fontSize: 16, fontWeight: 'bold', borderRadius: "5px 5px 0px 0px", padding: 10, color: 'white' }}>
                                            Company Details {this.state.companyVerified == 'Y' ? <span><i className="fa fa-check" style={{ color: 'green', fontSize: 20 }} aria-hidden="true" /></span> : null}
                                        </Card.Title>
                                        <Card.Body>
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>

                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Company Name</Form.Label>
                                                        <Form.Control type="text" placeholder="Company Name" value={this.state.companyName} />
                                                    </Form.Group>
                                                </div>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Office Address</Form.Label>
                                                        <Form.Control type="text" placeholder="Office Address" value={this.state.officeAddress} />
                                                    </Form.Group>
                                                </div>
                                            </div>
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>

                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <img src={this.state.companyReglink} width={200} />
                                                    </Form.Group>
                                                </div>
                                            </div>
                                            <div className="col-12">
                                                <Form.Group controlId="formBasicEmail" style={{ float: 'right' }}>
                                                   
                                                    {this.state.companyVerified == 'N' ? <Button variant="primary" type="button" onClick={this.approveAgent.bind(this, 'company')}>
                                                        Approve
                                                    </Button> : <Button variant="primary" type="button" onClick={this.invalidAgent.bind(this, 'company')}>
                                                        Invalid
                                                    </Button>
                                                    }
                                                </Form.Group>
                                            </div>
                                        </Card.Body>
                                    </Card>
                                </div>

                                <div className="col-lg-12 col-md-12" style={{ marginTop: 10 }}>
                                    <Card>
                                        <Card.Title className="bgColor" style={{ fontSize: 16, fontWeight: 'bold', borderRadius: "5px 5px 0px 0px", padding: 10, color: 'white' }}>
                                            Addhar Copy {this.state.adharVerified == 'Y' ? <span><i className="fa fa-check" style={{ color: 'green', fontSize: 20 }} aria-hidden="true" /></span> : null}
                                        </Card.Title>
                                        <Card.Body>
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <Form.Group controlId="formBasicEmail" >
                                                    <img src={this.state.addharLink} width={200} />
                                                </Form.Group>
                                            </div>

                                            <div className="col-12">
                                                <Form.Group controlId="formBasicEmail" style={{ float: 'right' }}>
                                                    
                                                    {this.state.adharVerified == 'N' ? <Button variant="primary" type="button" onClick={this.approveAgent.bind(this, 'addhar')}>
                                                        Approve
                                                    </Button> : <Button variant="primary" type="button" onClick={this.invalidAgent.bind(this, 'addhar')}>
                                                        Invalid
                                                    </Button>
                                                    }
                                                </Form.Group>
                                            </div>
                                        </Card.Body>
                                    </Card>
                                </div>
                                <div className="col-lg-12 col-md-12" style={{ marginTop: 10 }}>
                                    <Card>
                                        <Card.Title className="bgColor" style={{ fontSize: 16, fontWeight: 'bold', borderRadius: "5px 5px 0px 0px", padding: 10, color: 'white' }}>
                                            License Copy {this.state.licenseVerified == 'Y' ? <span><i className="fa fa-check" style={{ color: 'green', fontSize: 20 }} aria-hidden="true" /></span> : null}
                                        </Card.Title>
                                        <Card.Body>
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <Form.Group controlId="formBasicEmail" >
                                                    <img src={this.state.licenseLink} width={200} />
                                                </Form.Group>
                                            </div>
                                            <div className="col-12">
                                                <Form.Group controlId="formBasicEmail" style={{ float: 'right' }}>
                                                    
                                                    {this.state.licenseVerified == 'N' ? <Button variant="primary" type="button" onClick={this.approveAgent.bind(this, 'license')}>
                                                        Approve
                                                    </Button> : <Button variant="primary" type="button" onClick={this.invalidAgent.bind(this, 'license')}>
                                                        Invalid
                                                    </Button>
                                                    }
                                                </Form.Group>
                                            </div>
                                        </Card.Body>
                                    </Card>
                                </div>
                                <div className="col-lg-12 col-md-12" style={{ marginTop: 10 }}>
                                    <Card>
                                        <Card.Title className="bgColor" style={{ fontSize: 16, fontWeight: 'bold', borderRadius: "5px 5px 0px 0px", padding: 10, color: 'white' }}>
                                            RC Copy {this.state.rcVerified == 'Y' ? <span><i className="fa fa-check" style={{ color: 'green', fontSize: 20 }} aria-hidden="true" /></span> : null}
                                        </Card.Title>
                                        <Card.Body>
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <Form.Group controlId="formBasicEmail" >
                                                    <img src={this.state.rcLink} width={200} />
                                                </Form.Group>
                                            </div>
                                            <div className="col-12">
                                                <Form.Group controlId="formBasicEmail" style={{ float: 'right' }}>
                                                    {this.state.rcVerified == 'N' ? <Button variant="primary" type="button" onClick={this.approveAgent.bind(this, 'rc')}>
                                                        Approve
                                                    </Button> : <Button variant="primary" type="button" onClick={this.invalidAgent.bind(this, 'rc')}>
                                                        Invalid
                                                    </Button>
                                                    }
                                                </Form.Group>

                                            </div>
                                        </Card.Body>
                                    </Card>
                                </div>
                            </div>

                        </div>

                    </section>


                </div>
            </div>
        );
    }
}

export default withRouter(AgentDetails);